h1, h2, h3, h4, h5, h6 { /* Change heading typefaces here */ font-family: "Montserrat", sans-serif; font-weight: 700; margin-top: 2rem; margin-bottom: 0; line-height: 1.2; }

h1 { font-size: 5.4rem; margin-top: 4rem; }

